import Character from "./Character.js";
import { PositionedCharacter } from "./PositionedCharacter.js";
import Team from "./Team.js";
import Bowman from './characters/Bowman.js';
import Swordsman from './characters/Swordsman.js';
import Magician from './characters/Magician.js';
import Daemon from './characters/Daemon.js';
import Undead from './characters/Undead.js';
import Vampire from './characters/Vampire.js';


export default class GameState {
  static from(object) {
    //    
    const restoreTeam = (teamData, characterClasses) => {
      if (!teamData) return null;
      const team = new Team();
      team.characters = teamData.characters.map(charData => {
        const CharClass = characterClasses.find(cls => 
          cls.prototype.constructor.name.toLowerCase() === charData.type
        );
        if (!CharClass) {
          console.error('Character class not found:', charData.type);
          return charData;
        }
        const character = new CharClass(charData.level);
        Object.assign(character, charData);
        return character;
      });
      return team;
    };
  
    //   
    const restorePositioned = (chars, team) => {
      return chars.map(pc => {
        const char = team?.characters?.find(c => 
          c.type === pc.character.type && 
          c.level === pc.character.level
        ) || pc.character;
        return new PositionedCharacter(char, pc.position);
      });
    };
  
    return new GameState(
      object.currentTurn,
      object.maxScore,
      object.currentLevel,
      restoreTeam(object.playerTeam, [Bowman, Swordsman, Magician]),
      restoreTeam(object.enemyTeam, [Daemon, Undead, Vampire]),
      restorePositioned(object.positionedPlayerCharacters, object.playerTeam),
      restorePositioned(object.positionedEnemyCharacters, object.enemyTeam)
    );
  }

  constructor(
    currentTurn = 'player',
    maxScore = 0,
    currentLevel = 1,
    playerTeam = null,
    enemyTeam = null,
    positionedPlayerCharacters = [],
    positionedEnemyCharacters = []
  ) {
    this.currentTurn = currentTurn;
    this.maxScore = maxScore;
    this.currentLevel = currentLevel;
    this.playerTeam = playerTeam;
    this.enemyTeam = enemyTeam;
    this.positionedPlayerCharacters = positionedPlayerCharacters;
    this.positionedEnemyCharacters = positionedEnemyCharacters;
  }

  serialize() {
    const safeSerialize = (obj) => {
      if (Array.isArray(obj)) {
        return obj.map(item => safeSerialize(item));
      }
      if (obj instanceof PositionedCharacter) {
        return {
          character: safeSerialize(obj.character),
          position: obj.position
        };
      }
      if (obj instanceof Character) {
        const serialized = {};
        Object.keys(obj).forEach(key => {
          if (typeof obj[key] !== 'function' && key !== 'actions' && key !== 'moveCost') {
            serialized[key] = obj[key];
          }
        });
        serialized.type = obj.type; 
        return serialized;
      }
      return obj;
    };
    
    return safeSerialize({
      currentTurn: this.currentTurn,
      maxScore: this.maxScore,
      currentLevel: this.currentLevel,
      playerTeam: this.playerTeam,
      enemyTeam: this.enemyTeam,
      positionedPlayerCharacters: this.positionedPlayerCharacters,
      positionedEnemyCharacters: this.positionedEnemyCharacters,
      isGameLocked: false 
    });
  }
}